<template>
    <div>
        <el-table
                :data="tableData"
                border
                style="width: 100%;"
                :header-cell-style="headClass"
                :cell-style="rowClass">
            <el-table-column
                    prop="id"
                    label="id" style="text-align: center">
            </el-table-column>
            <el-table-column
                    prop="account"
                    label="" >
            </el-table-column>
            <el-table-column
                    prop="password"
                    label="">
            </el-table-column>
            <el-table-column
                    prop="count"
                    label="">
            </el-table-column>
            <el-table-column
                    prop="createTime"
                    label="">
            </el-table-column>
            <el-table-column
                    prop="type"
                    label="">
            </el-table-column>
            <el-table-column
                    label="" >
                <template slot-scope="scope">
                    <el-button  @click="handleClick(scope.row.account)" type="text" icon="el-icon-delete" size="small"></el-button>
                    <el-button  @click="addPublic(scope.row)" type="text" icon="el-icon-edit" size="small"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-pagination
                style="text-align: center;margin-top: 20px"
                background
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="page"
                :page-sizes="[5, 40, 80, 100]"
                :page-size="count"
                layout="total, sizes, prev, pager, next, jumper"
                :page-count="pages"
                :total="total">
        </el-pagination>

        <el-dialog
                :visible.sync="dialogVisible"
                width="30%">
            <el-form status-icon  ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="" >
                    <el-input type="account" v-model="current.account" :disabled="true" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" >
                    <el-input type="count" v-model="current.count" :disabled="true" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="/">
                    <el-input v-model="addCount" oninput="value=value.replace(/[^(\-)0-9]/g,'')"   placeholder="" ></el-input>
                </el-form-item>
            </el-form>
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click="dialogVisibleClick(current.account)"></el-button>
        </el-dialog>

    </div>
</template>

<script>
    export default {
        created() {
            this.update(this.page,this.count);
        },
        methods: {
            addPublic(row){
                this.dialogVisible = true
                this.current = row
            },
            dialogVisibleClick(account){
                if((parseInt(this.current.count) + parseInt(this.addCount)) >= 0){
                    this.dialogVisible = false
                    var that = this
                    this.$http.post('admin/addUserCount?account=' + account + '&count=' + this.addCount)
                        .then(function (response) {
                            that.$load(0)
                            if(response.data.code == 0){
                                that.update(that.page,that.count);
                                that.$message.success(response.data.message)
                            }else {
                                that.$message.error(response.data.message)
                            }
                    })
                }else {
                    this.$message.error("")
                }
            },
            handleSizeChange(val) {
                console.log("" + val)
                this.count = val
                this.update(this.page,this.count);
            },
            handleCurrentChange(val) {
                this.page = val
                this.update(this.page,this.count);
                console.log(`: ${val}`);
            },
            handleClick(row) {
                var that = this
                this.$http.post('admin/dele?account=' + row)
                    .then(function (response) {
                        that.$load(0)
                        if(response.data.code == 0){
                            that.update(that.page,that.count);
                        }else {
                            that.$message.error(response.data.message)
                        }
                    })
                    .catch(function (error) {
                        that.$load(0)
                        that.$message,error("")
                    });
            },
            update(pageNum,pageSize){
                var that = this
                that.$load(1,"")
                this.$http.get('/admin/queryAll?pageNum=' + pageNum + '&pageSize=' + pageSize)
                    .then(function (response) {
                        that.$load(0)
                        if(response.data.code == 0){
                            console.log(response.data.data)
                            that.tableData = []
                            for (let res in response.data.data){
                                var temp = response.data.data[res]
                                if(temp.type === 1){
                                    temp.type = ""
                                }else {
                                    temp.type = ""
                                }
                                that.tableData.push(temp)
                            }
                            that.pages = response.data.pages
                            that.total = response.data.total
                        }else {
                            that.$message.error(response.data.message)
                        }
                    })
                    .catch(function (error) {
                        that.$load(0)
                        that.$message,error("")
                    });
            },
            headClass() { //
                return "text-align:center"
            },
            rowClass() { //
                return "text-align:center"
            }
        },
        data() {
            return {
                tableData: [],
                count: 5,
                page: 1,
                pages: 0,
                total: 1,
                dialogVisible:false,
                addCount: 1,
                current:{}
            }
        }
    }
</script>

<style lang="less" >
    .el-pagination{
        .el-input__inner{
            height: 30px;
        }
    }
</style>
