<template>
    <div>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm"   label-width="100px" class="demo-ruleForm">
            <el-form-item label="" prop="appName">
                <el-input  v-model="ruleForm.appName"></el-input>
            </el-form-item>

            <el-form-item label="" prop="url">
                <el-input v-model="ruleForm.url"></el-input>
            </el-form-item>

            <el-form-item label="" prop="name">
                <el-input v-model="ruleForm.name"></el-input>
            </el-form-item>

            <el-form-item label="" prop="organization">
                <el-input v-model="ruleForm.organization"></el-input>
            </el-form-item>

            <el-form-item label="" prop="describe">
                <el-input v-model="ruleForm.describe"></el-input>
            </el-form-item>

            <el-form-item label="" prop="consentMessage">
                <el-input v-model="ruleForm.consentMessage"></el-input>
            </el-form-item>

            <el-form-item label="" prop="icon">
                <input class="file"  name="fileIocn" type="file" accept="image/png,image/jpeg" @change="getFileIcon($event)"/>
            </el-form-item>

            <el-form-item label="" prop="startIcon">
                <input class="file"  name="fileStartIcon" type="file" accept="image/png,image/jpeg" @change="getFileStartIcon($event)"/>
            </el-form-item>

            <el-form-item label="" prop="isRemove">
                <el-radio-group v-model="ruleForm.isRemove">
                    <el-radio label="1"></el-radio>
                    <el-radio label="0"></el-radio>
                </el-radio-group>
            </el-form-item>

            <el-form-item label="" prop="isRemove">
                <el-radio-group v-model="ruleForm.isVariable">
                    <el-radio label="1"></el-radio>
                    <el-radio label="0"></el-radio>
                </el-radio-group>
            </el-form-item>

            <el-form-item label="" prop="pageName">
                <el-input placeholder="xxx.xxx.xx,"  v-model="ruleForm.pageName"></el-input>
            </el-form-item>

            <el-form-item label="" prop="version">
                <el-input v-model="ruleForm.version" placeholder=":1.1"></el-input>
            </el-form-item>

            <el-form-item label=""  prop="rootCert">
                <el-input :rows="5" placeholder="" type="textarea" v-model="ruleForm.rootCert"></el-input>
            </el-form-item>

            <el-form-item label="" prop="serverCert">
                <el-input :rows="5"  placeholder="" type="textarea"  v-model="ruleForm.serverCert"></el-input>
            </el-form-item>

            <el-form-item label=""  prop="keyCert">
                <el-input :rows="5" placeholder=""  type="textarea"  v-model="ruleForm.keyCert"></el-input>
            </el-form-item>

            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import axios from 'axios'

    var iconIs = null
    export default {
        created () {

        },
        data () {
            var validatePass = (rule, value, callback) => {
                if (iconIs == null) {
                    callback(new Error())
                } else {
                    callback()
                }
            }
            return {
                ruleForm: {
                    account: '',
                    appName: '',
                    url: axios.defaults.baseURL,
                    name: '',
                    organization: '',
                    describe: '',
                    consentMessage: '',
                    icon: '',
                    startIcon: '',
                    isRemove: '1',
                    isVariable:"0",
                    pageName: '',
                    version: '',
                    isXfive: '1',
                    rootCert: '',
                    serverCert: '',
                    keyCert: ''
                },
                fullscreenLoading: false,
                rules: {
                    appName: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    url:[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    icon: [
                        { validator: validatePass, message: '', trigger: 'blur' }
                    ],
                    pageName: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    version: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    pageName: [
                        { required: true, message: '', trigger: 'blur' }
                    ]
                }
            }
        },
        methods: {
            submitForm (formName) {
                const that = this
                this.$refs[formName].validate((valid) => {
                    if (valid) {

                        const formData = new FormData()
                        console.log('niha1')
                        console.log(that.ruleForm.icon)

                        formData.append('icon', that.ruleForm.icon)
                        formData.append('startIcon', that.ruleForm.startIcon)
                        const icon = that.ruleForm.icon
                        const startIcon = that.ruleForm.startIcon
                        that.ruleForm.icon = ''
                        that.ruleForm.startIcon = ''

                        formData.append('statusJson', JSON.stringify(that.ruleForm))
                        that.ruleForm.icon = icon
                        that.ruleForm.startIcon = startIcon

                        const config = {
                            headers: {
                                'Content-Type': 'multipart/form-data'
                            }
                        }
                        this.$load(1, '')
                        this.$http.post('/pack/submit', formData, config).then(function (response) {
                            if (response.status === 200) {
                                that.$message.success(response.data.message)
                            }
                            that.$load(0)
                        })
                    } else {
                        console.log('error submit!!')
                        return false
                    }
                })
            },
            getFileIcon (event) {
                this.ruleForm.icon = event.target.files[0]
                iconIs = '123'
            },
            getFileStartIcon (event) {
                this.ruleForm.startIcon = event.target.files[0]
            }
        }
    }
</script>

<style scoped>

</style>
